.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogItems {
    padding: 10px;
    /* text-decoration: none; */
}

.dialogItems a {
    text-decoration: none;
}

.dialog {
    color:white;
}

.dialogItems .active {
    color: rgb(3, 172, 45);
}

.messages {
    padding: 10px;
    color: wheat;
}

.message {
    color: black;
}