.profilePicture {
    width: 100px;
    height: 100px;
    border-radius: 50px;
}

.userItem {
    margin-left: 10px;
    padding: 10px;
    margin-top: 10px;
    border: 1px solid rgb(79, 147, 47);
    border-radius: 30px;
    max-width: 70%;
    max-height: 100%;
}

.userName {
    display: block;
    position: absolute;
    text-align: left;
    left: 350px;
}

.location {
    text-align: left;
    position: absolute;
    left: 980px;
}

.selectedPage {
    color: gold;
}

.selectPageButton {
    margin: 10px;
    padding: 3px;
    display: inline-block;
    background-color: #20bf6b;
    cursor: pointer;
    font-size: 20px;
}